import React, { PureComponent } from 'react';
import { MineWrapper } from './style';
import {Link} from 'react-router-dom';

class Mine extends PureComponent{
    constructor(props) {
        super(props);
        this.state = {
            list: [
                {
                    title: '',
                    icon: '&#xe72d;'
                },
                {
                    title: '',
                    icon: '&#xe670;'
                },
                {
                    title: '',
                    icon: '&#xe68e;'
                },
                {
                    title: '',
                    icon: '&#xe615;'
                },
                {
                    title: '',
                    icon: '&#xe70b;'
                }
            ],
            userList: [
                {
                    title: "",
                    desc: "2"
                },
                {
                    title: "",
                    desc: "0"
                },
                {
                    title: "",
                    desc: ""
                },
                {
                    title: "",
                    desc: ""
                },
                {
                    title: "APP",
                    desc: ""
                }
            ]
        }
    }
    render() {
        return (
            <MineWrapper imgSrc={'https://w.kl.126.net/public/mykaola-mobile/6d111bc9fc56e0026c4093e47e330116.png'}>
                <div className="header">
                    <div className="title"></div>
                    <Link to="/"><span className="iconfont red">&#xe61a;</span></Link>
                    <Link to="/cart"><span className="iconfont">&#xe607;</span></Link>
                </div>
                <div className="user">
                    <img src="http://haitao.nos.netease.com/vtgcByWTZlKwtnRQvhvLT170102401020_150_150.png" alt=""/>
                    <p>7721</p>
                </div>
                <div className="order">
                    <div className="title">
                        <p className="left"></p>
                        <p className="right"><span className="iconfont">&#xe634;</span></p>
                    </div>
                    <div className="list">
                        {
                            this.state.list.map((item, index)=> (
                                <div className="item" key={index}>
                                    <span className="iconfont" dangerouslySetInnerHTML={{__html:item.icon}}></span>
                                    <span className="desc">{item.title}</span>
                                </div>
                            ))
                        }
                    </div>
                    <p className="fenge"></p>
                </div>
                <div className="userList">
                    {
                        this.state.userList.map((item, index) => (
                            <div className="item" key={index}>
                                <span className="title">{item.title}</span>
                                <span className="desc iconfont">{item.desc}&#xe634;</span>
                            </div>
                        ))
                    }
                </div>
                <p className="user_btn">  |    |  7721  |  </p>
                <p className="user_bottom">@1997-2019</p>
            </MineWrapper>
        )
    }
}

export default Mine;